<template>
  Hello {{user}}

  <div class="flex h-screen overflow-hidden">
    <div>
      <div id="sidebar" class="flex flex-col absolute z-40 left-0 top-0 lg:static lg:left-auto lg:top-auto lg:translate-x-0 transform h-screen overflow-y-scroll lg:overflow-y-auto no-scrollbar w-64 lg:w-20 lg:sidebar-expanded:!w-64 2xl:!w-64 shrink-0 bg-slate-800 p-4 transition-all duration-200 ease-in-out -translate-x-64">

      </div>
    </div>

    <div class="relative flex flex-col flex-1 overflow-y-auto overflow-x-hidden">
      <header class="sticky top-0 bg-white border-b border-slate-200 z-30">
        
      </header>
    </div>
  </div>

  <table>
    <tbody>
      <tr class="w-full lg:w-1/3 m-2" v-for="post in posts">
        <td>
          <h3>
            {{ post.title }}
          </h3>
        </td>

        <td>
          {{ post.createdAt }}
        </td>

        <td>
          {{ post.intro }}
        </td>
      </tr>
    </tbody>
  </table>

</template>

<script>
import axios from 'axios';

export default {
  name: "App",
  data() {
    return {
      user: 'John',
      posts: []
    }
  },
  methods: {
    load() {
      axios.get(`https://62d4757a5112e98e484fa7f0.mockapi.io/posts`)
      .then((response) => {
        this.posts = response.data;
      });
    }
  },
  mounted() {
    this.load();
  }
}
</script>

